import InputView from "../input/InputView";
import * as rs from './RenderState'
import * as textUtil from '../text/TextVMUtil'
import TitleRenderItem from "./TitleRenderItem";
import { Button } from "antd";

export const get = (state, render) => {
  var qI = new TitleRenderItem();
  qI.State = state;
  qI.Render = render;
  return qI;
}

export const GetRender = (q) => {
  switch (q.State) {
    case rs._default:
      return (title, column, table) => {
        return <>
          <div style={{ display: 'flex' }}>
            {title}
            {table.CanEditColumn ?
             <Button type='primary' size='small'
              style={{ backgroundColor: '#faad14', border: 'none' }}
              onClick={column.Edit}>edit</Button> : ''}
            {table.CanDeleteColumn ? <Button type='primary' danger size='small'
              onClick={column.Delete}>x</Button> : '' }
          </div>
        </>
      };
    case rs.edit:
      return (title, column, table) => {
        var inputVM = textUtil.get(title, 'input');
        inputVM.onSetContent = (v) => {
          column.Title = v;
        }
        return <>
          <div style={{ display: 'flex' }}>
            <InputView store={inputVM} />
            <Button type='primary' size='small'
              onClick={column.OK}>ok</Button>
          </div>
        </>
      };
    default:
      return undefined;
  }
}