import * as rowUtil from './TableRowUtil'
import * as colUtil from './TableColumnUtil'
import TableVM from './TableVM';

export const get = (data) => {
  var qI = new TableVM();
  qI.Columns = data.columns.map(x => colUtil.GetByTitle(x, qI));
  qI.DataSource = data.dataSource;
  return qI;
}

export const GetRows = (q) => {
  return q.DataSource.map(x => rowUtil.get(x, q));
}