import { makeObservable, observable, action } from 'mobx';
import * as rs from './RenderState'
import * as renderUtil from './RenderItemUtil'
import Render_Dict from './Render_Dict';
import TitleRender_Dict from './TitleRender_Dict';

export default class TableColumn {
  constructor() {
    makeObservable(this, {
      Title : observable,
      TitleRenderState : observable
    })
  }

  get Index(){
    return this.Table.Columns.indexOf(this);
  }

  TitleRenderState = rs._default
  RenderState = rs._default;

  Title = undefined

  OnSetTitleOK_Funcs = []

  titleRender_Dict
  get TitleRender_Dict(){
    if (!this.titleRender_Dict){
      this.titleRender_Dict = new TitleRender_Dict();
    }
    return this.titleRender_Dict;
  }

  render_Dict
  get Render_Dict(){
    if (!this.render_Dict){
      this.render_Dict = new Render_Dict();
    }
    return this.render_Dict;
  }
  set Render_Dict(v){
    this.render_Dict = v;
  }

  Edit = () => {
    this.TitleRenderState = rs.edit;
  }

  OK = () => {
    this.TitleRenderState = rs._default;
    this.OnSetTitleOK_Funcs.forEach(x => x(this));
  }

  Delete = () => {
    this.Table.Columns.splice(this.Index, 1);
    this.Table.Update();
  }

  TitleRender = () => {
    var title = this.Title;
    var table = this.Table;

    var render = this.TitleRender_Dict.GetRender(this.TitleRenderState);
    return render.Render(title, this, table);
  }
}