import TableColumn from "./TableColumn"
import * as renderUtil from './RenderItemUtil'
import { Button } from "antd";
import * as rs from './RenderState'
import * as genUtil from '../../util/generalUtil'

export const get = (title, dataIndex, renders, table) => {
  var qI = new TableColumn();
  qI.Title = title;

  if (Array.isArray(dataIndex)) {
    qI.DataIndex = dataIndex;
  } else {
    qI.DataIndex = [dataIndex];
  }

  qI.Key = genUtil.randomId();
  qI.Table = table;

  var dict = qI.Render_Dict;
  if (renders) {
    renders.forEach(x => {
      dict.GetRender(x.State).Render = x.Render
    });
  }

  return qI;
}

export const GetByTitle = (title, table) => {
  var qI = new TableColumn();
  qI.Title = title;
  qI.DataIndex = [title];
  qI.Key = genUtil.randomId();
  qI.Table = table;


  qI.OnSetTitleOK_Funcs.push(() => {
    qI.DataIndex = [qI.Title];
    table.Update();
  })

  return qI;
}

export const GetActionColumn = (table) => {
  var title = undefined;

  var renders = [
    renderUtil.get(rs._default, (val, item, dataIndex, cell, row) => {
      var editElem = '';
      if (table.CanEdit) {
        var edit = () => row.Edit();
        editElem = <Button type='primary' size='small'
          style={{ backgroundColor: '#faad14', border: 'none' }}
          onClick={edit}>edit</Button>
      }

      var delElem = '';
      if (table.CanDelete) {
        var del = () => table.DeleteItem(item, row);

        delElem = <Button type='primary' danger size='small'
          onClick={del}>x</Button>
      }
      return <>
        {editElem}{delElem}
      </>
    }),
    renderUtil.get(rs.edit, (val, item, dataIndex, cell, row) => {
      var ok = () => row.OK();
      return <Button type='primary' size='small'
        onClick={ok}>ok</Button>
    })
  ]

  var col = get(title, null, renders, table);

  if (table.CanAdd) {
    col.TitleRender_Dict.GetRender(rs._default).Render = () =>
      <div style={{ display: 'flex' }}>
        <Button type='primary' size='small'
          onClick={table.AddColumn}>
          (+) c
        </Button>
        <Button type='primary' size='small'
          onClick={table.AddItem}>
          (+) r
      </Button>
      </div>
  }

  return col;
}