import TableCell from "./TableCell"

export const get = (row, col) => {
  var qI = new TableCell();
  qI.Row = row;
  qI.Column = col;
  return qI;
}

export const GetValue = (q) => {
  var dataIndex = q.DataIndex;
  var item = q.Item;

  var value = undefined;

  if (dataIndex) {
    value = item;
    dataIndex.forEach(x => {
      value = value[x]
    })
  }

  return value;
}